import { Component } from '@angular/core';
import { NavController, NavParams, ViewController } from 'ionic-angular';
import { TabsPage } from "../tabs/tabs";
import { AjustesProvider } from "../../providers/ajustes/ajustes";


@Component({
	selector: 'page-introduccion',
	templateUrl: 'introduccion.html',
})
export class IntroduccionPage {
	slides = [
	{
		title: "Mensaje de la presidenta",
		description: "Me complace que haya llegado el momento de celebrar nuestro tradicional Congreso de Radioterapia, en el que colegas  y  familiares  compartimos por 4 das los retos, logros y oportunidades en el amplio campo de la radioterapia.",
		image: "assets/img/FotoPresidenta.png",
		fin: false,
	},
	{
		title: "",
		description: "El comit organizador ha pensado con cuidado y por varios meses el contenido del programa acadmico que aqu presentamos, en el cual incluimos a reconocidos expertos de Mxico y el mundo que expondrn los temas que consideramos ms relevantes y de vanguardia a nivel global para mejorar los tratamientos, y continuar con el desarrollo acadmico de nuestra disciplina. Entre los rubros a destacar, contaremos con hipofraccionamiento en mama, recto, prstata, as como los avances que presenta la inmunoterapia y resultados de estudios en cncer de cabeza y cuello. Adems del taller de contorneo y de los programas de Fsica Mdica y de los jvenes.",
		image: "assets/img/FotoPresidenta.png",
		fin: false,
	},
	{
		title: "",
		description: "Quiero agradecer a todos los miembros de SOMERA, a la mesa directiva, al comit organizador, expositores, acadmicos, al total de la sociedad mdica y a la industria, sin la cual tambin sera imposible tener este evento. Espero que disfrutemos estos das en el entorno de las bellas playas de Puerto Vallarta.",
		image: "assets/img/FotoPresidenta.png",
		fin: true,
	}
	];
	constructor(public navCtrl: NavController, public navParams: NavParams, private ajustes: AjustesProvider, private viewCtrl: ViewController) {
	}

	saltar_tutorial(){
		if(this.ajustes.ajustes.mostrar_introduccion==true) {
			this.ajustes.ajustes.mostrar_introduccion=false;
			//this.ajustes.guardar_storage();
			this.navCtrl.setRoot(TabsPage);
		}else{
			this.viewCtrl.dismiss();
		}		
	}
}



